.page {
  margin: 0;
  padding: 0;
}

.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minute-hand {
  box-sizing: border-box;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  bottom: 50%;
  left: 48%;
  transform: rotate(0);
}

.stopwatch__second-hand {
  box-sizing: border-box;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: rotate(0);
}

.stopwatch__center-point {
  box-sizing: border-box;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch.stopwatch--speed-up .stopwatch__second-hand {
  animation: 9.95s linear infinite rotate;
}

.stopwatch.stopwatch--speed-up .stopwatch__minute-hand {
  animation: 600s steps(60, end) infinite rotate;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
